import * as XLSX from 'xlsx'
import { saveAs } from 'file-saver'

/**
 * Excel
 * Excel
 */
export class ExcelExporter {
  /**
   * 
   * @param {Array} data - 
   * @param {Object} searchParams - 
   * @param {string} filename - 
   */
  static exportCigaretteData(data, searchParams = {}, filename = '') {
    try {
      // 
      if (!data || !Array.isArray(data) || data.length === 0) {
        throw new Error('')
      }

      // 
      const timestamp = new Date().toISOString().slice(0, 19).replace(/:/g, '-')
      const defaultFilename = `_${timestamp}.xlsx`
      const finalFilename = filename || defaultFilename

      // 
      const headers = this.generateHeaders()
      
      // 
      const tableData = this.prepareTableData(data, headers)
      
      // 
      const workbook = XLSX.utils.book_new()
      
      // 
      const worksheet = XLSX.utils.aoa_to_sheet(tableData)
      
      // 
      this.setColumnWidths(worksheet, headers)
      
      // 
      this.setWorksheetStyles(worksheet, tableData.length)
      
      // 
      XLSX.utils.book_append_sheet(workbook, worksheet, '')
      
      // 
      if (Object.keys(searchParams).length > 0) {
        this.addSearchInfoSheet(workbook, searchParams)
      }
      
      // 
      const excelBuffer = XLSX.write(workbook, { 
        bookType: 'xlsx', 
        type: 'array' 
      })
      
      const blob = new Blob([excelBuffer], { 
        type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' 
      })
      
      saveAs(blob, finalFilename)
      
      return {
        success: true,
        message: 'Excel',
        filename: finalFilename
      }
      
    } catch (error) {
      console.error('Excel:', error)
      return {
        success: false,
        message: `: ${error.message}`,
        error: error
      }
    }
  }

  /**
   * 
   * @returns {Array} 
   */
  static generateHeaders() {
    const baseHeaders = [
      '',
      '', 
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      ''
    ]
    
    // 30301
    const positionHeaders = []
    for (let i = 30; i >= 1; i--) {
      positionHeaders.push(`${i}`)
    }
    
    // 
    const remarkHeader = ['']
    
    return [...baseHeaders, ...positionHeaders, ...remarkHeader]
  }

  /**
   * 
   * @param {Array} data - 
   * @param {Array} headers - 
   * @returns {Array} 
   */
  static prepareTableData(data, headers) {
    const tableData = []
    
    // 
    tableData.push(headers)
    
    // 
    data.forEach(item => {
      const row = [
        item.cigCode || '',
        item.cigName || '',
        item.year || '',
        item.month || '',
        item.weekSeq || '',
        item.encodedExpression || '',
        item.decodedExpression || '',
        item.deliveryArea || '',
        item.advAmount || 0,
        item.actualDelivery || 0
      ]
      
      // 30301
      for (let i = 30; i >= 1; i--) {
        const dKey = `d${i}`
        row.push(item[dKey] || 0)
      }
      
      // 
      row.push(item.remark || '')
      
      tableData.push(row)
    })
    
    return tableData
  }

  /**
   * 
   * @param {Object} worksheet - 
   * @param {Array} headers - 
   */
  static setColumnWidths(worksheet, headers) {
    const columnWidths = {
      '': 12,
      '': 25,
      '': 8,
      '': 8,
      '': 10,
      '': 25,
      '': 40,
      '': 30,
      '': 12,
      '': 12,
      '': 20
    }
    
    // 
    headers.forEach((header, index) => {
      const colKey = XLSX.utils.encode_col(index)
      if (columnWidths[header]) {
        worksheet[`${colKey}1`] = { 
          ...worksheet[`${colKey}1`],
          w: columnWidths[header]
        }
      } else if (header.includes('')) {
        // 
        worksheet[`${colKey}1`] = { 
          ...worksheet[`${colKey}1`],
          w: 8
        }
      }
    })
  }

  /**
   * 
   * @param {Object} worksheet - 
   * @param {number} dataRowCount - 
   */
  static setWorksheetStyles(worksheet, dataRowCount) {
    const range = XLSX.utils.decode_range(worksheet['!ref'])
    
    // 
    for (let col = range.s.c; col <= range.e.c; col++) {
      const colKey = XLSX.utils.encode_col(col)
      const headerCell = worksheet[`${colKey}1`]
      
      if (headerCell) {
        headerCell.s = {
          font: { bold: true, color: { rgb: 'FFFFFF' } },
          fill: { fgColor: { rgb: '4472C4' } },
          alignment: { horizontal: 'center', vertical: 'center' },
          border: {
            top: { style: 'thin' },
            bottom: { style: 'thin' },
            left: { style: 'thin' },
            right: { style: 'thin' }
          }
        }
      }
    }
    
    // 
    for (let row = 2; row <= dataRowCount + 1; row++) {
      for (let col = range.s.c; col <= range.e.c; col++) {
        const colKey = XLSX.utils.encode_col(col)
        const cell = worksheet[`${colKey}${row}`]
        
        if (cell) {
          // 
          if (col >= 10 && col <= 39) { // 11400-based2
            cell.s = {
              alignment: { horizontal: 'center', vertical: 'center' },
              border: {
                top: { style: 'thin' },
                bottom: { style: 'thin' },
                left: { style: 'thin' },
                right: { style: 'thin' }
              }
            }
          } else {
            cell.s = {
              border: {
                top: { style: 'thin' },
                bottom: { style: 'thin' },
                left: { style: 'thin' },
                right: { style: 'thin' }
              }
            }
          }
        }
      }
    }
  }

  /**
   * 
   * @param {Object} workbook - 
   * @param {Object} searchParams - 
   */
  static addSearchInfoSheet(workbook, searchParams) {
    const infoData = [
      [''],
      [''],
      ['', new Date().toLocaleString('zh-CN')],
      ['', searchParams.year || ''],
      ['', searchParams.month || ''],
      ['', searchParams.weekSeq || ''],
      [''],
      [''],
      ['30'],
      [''],
      ['' + new Date().toLocaleString('zh-CN')]
    ]
    
    const infoWorksheet = XLSX.utils.aoa_to_sheet(infoData)
    
    // 
    infoWorksheet['A1'] = { ...infoWorksheet['A1'], w: 20 }
    infoWorksheet['B1'] = { ...infoWorksheet['B1'], w: 30 }
    
    // 
    infoWorksheet['A1'].s = {
      font: { bold: true, size: 14, color: { rgb: '4472C4' } }
    }
    
    XLSX.utils.book_append_sheet(workbook, infoWorksheet, '')
  }

  /**
   * 
   * @param {Object} positionData - 
   * @param {Object} cigaretteInfo - 
   * @param {string} filename - 
   */
  static exportPositionData(positionData, cigaretteInfo = {}, filename = '') {
    try {
      if (!positionData || Object.keys(positionData).length === 0) {
        throw new Error('')
      }

      const timestamp = new Date().toISOString().slice(0, 19).replace(/:/g, '-')
      const defaultFilename = `_${cigaretteInfo.cigName || ''}_${timestamp}.xlsx`
      const finalFilename = filename || defaultFilename

      // 
      const workbook = XLSX.utils.book_new()
      
      // 
      const positionTableData = this.preparePositionTableData(positionData, cigaretteInfo)
      
      // 
      const worksheet = XLSX.utils.aoa_to_sheet(positionTableData)
      
      // 
      worksheet['A1'] = { ...worksheet['A1'], w: 15 }
      worksheet['B1'] = { ...worksheet['B1'], w: 12 }
      
      // 
      worksheet['A1'].s = {
        font: { bold: true, color: { rgb: 'FFFFFF' } },
        fill: { fgColor: { rgb: '4472C4' } },
        alignment: { horizontal: 'center', vertical: 'center' }
      }
      
      worksheet['B1'].s = {
        font: { bold: true, color: { rgb: 'FFFFFF' } },
        fill: { fgColor: { rgb: '4472C4' } },
        alignment: { horizontal: 'center', vertical: 'center' }
      }
      
      // 
      XLSX.utils.book_append_sheet(workbook, worksheet, '')
      
      // 
      const excelBuffer = XLSX.write(workbook, { 
        bookType: 'xlsx', 
        type: 'array' 
      })
      
      const blob = new Blob([excelBuffer], { 
        type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' 
      })
      
      saveAs(blob, finalFilename)
      
      return {
        success: true,
        message: 'Excel',
        filename: finalFilename
      }
      
    } catch (error) {
      console.error('Excel:', error)
      return {
        success: false,
        message: `: ${error.message}`,
        error: error
      }
    }
  }

  /**
   * 
   * @param {Object} positionData - 
   * @param {Object} cigaretteInfo - 
   * @returns {Array} 
   */
  static preparePositionTableData(positionData, cigaretteInfo) {
    const tableData = [
      ['', '']
    ]
    
    // 301
    for (let i = 30; i >= 1; i--) {
      const positionKey = `position${i}`
      const value = positionData[positionKey] || 0
      tableData.push([`${i}`, value])
    }
    
    // 
    if (cigaretteInfo.cigName) {
      tableData.unshift(['', cigaretteInfo.cigName])
      tableData.unshift([''])
    }
    
    return tableData
  }
}

export default ExcelExporter
