          clearable
          :disabled="isFormDisabled"
          @change="handleDistributionTypeChange"
        >
          <el-option label="" value="" />
          <el-option label="" value="" />
        </el-select>
      </el-form-item>
      
      <el-form-item label="" v-if="searchForm.distributionType === ''">
        <el-select
          v-model="searchForm.extendedType"
