        />
      </el-form-item>
      
      <el-form-item label="" v-if="selectedRecord && selectedRecord.cigCode">
        <el-input
          :value="formatQuantity(selectedRecord.actualDelivery)"
          style="width: 150px"
          readonly
          suffix-icon="el-icon-info"
        />
      </el-form-item>
      
      <!--  -->
      <div v-if="selectedRecord && selectedRecord.cigCode" class="position-settings-section">
        <div class="position-header">
        <el-divider content-position="left">
          <el-icon><Setting /></el-icon>
          
        </el-divider>
        
          <div class="position-view-toggle">
            <el-button-group>
              <el-button 
                :type="positionViewMode === 'grid' ? 'primary' : ''"
                size="small"
                @click="switchPositionView('grid')"
              >
                <el-icon><DataBoard /></el-icon>
                
              </el-button>
              <el-button 
                :type="positionViewMode === 'encoding' ? 'primary' : ''"
                size="small"
                @click="switchPositionView('encoding')"
              >
                <el-icon><Document /></el-icon>
                
              </el-button>
              <el-button 
                size="small"
                @click="switchPositionView('3d')"
                :disabled="!hasChartData"
              >
                <el-icon><TrendCharts /></el-icon>
                
              </el-button>
            </el-button-group>
          </div>
        </div>
        
        <!--  -->
        <div v-if="positionViewMode === 'grid'" class="position-grid">
          <div v-for="(position, index) in positionData" :key="`d${30 - index}`" class="position-item">
            <span class="position-label">D{{ 30 - index }}:</span>
            <el-input-number
              v-model="positionData[index]"
              :min="0"
              :precision="0"
              :step="1"
              size="small"
              style="width: 90px"
              :disabled="isFormDisabled"
              @change="handlePositionChange"
            />
          </div>
        </div>
        
        <!--  -->
        <el-dialog
          v-model="show3DChart"
          :title="`${(selectedRecord && selectedRecord.cigName) || ''} - `"
          width="95%"
          height="85vh"
          :destroy-on-close="true"
          :close-on-click-modal="false"
          :close-on-press-escape="true"
          :show-close="true"
          draggable
          class="chart-dialog"
          @opened="handleDialogOpened"
        >
          <Position3DChart 
            ref="position3DChart"
            :selectedRecord="selectedRecord"
            :tableData="tableData"
            style="height: 1125px;"
          />
          <template #footer>
            <div class="dialog-footer">
              <el-button @click="show3DChart = false"></el-button>
              <el-button 
                type="primary" 
                @click="exportChart"
                :disabled="!hasChartData"
              >
                
              </el-button>
            </div>
          </template>
        </el-dialog>
        
        <!--  () -->
        <div v-if="positionViewMode === 'encoding'" class="encoded-expression-section">
          <el-divider content-position="left">
            <el-icon><Document /></el-icon>
            
          </el-divider>
          
          <div class="encoded-expression-input-container">
            <el-form-item label="" class="encoded-expression-form-item">
              <el-input
                v-model="encodedExpressionInput"
                type="textarea"
                :rows="6"
