<template>
  <div class="position-3d-chart-container">
    <div v-if="!hasData" class="no-data-message">
      <el-empty description="" />
    </div>
    <div v-else class="chart-wrapper">
      <div ref="chartContainer" class="chart-container"></div>
      <div v-if="uniqueAmounts.length > 1" class="chart-legend">
        <div class="legend-title"></div>
        <div class="legend-items">
          <div 
            v-for="(amount, index) in uniqueAmounts" 
            :key="amount"
            class="legend-item"
          >
            <span 
              class="legend-color-box"
              :style="{ backgroundColor: colorMap.get(amount) }"
            ></span>
            <span class="legend-text">{{ amount }}</span>
            <span class="legend-type">{{ index % 2 === 0 ? '()' : '()' }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts'
import 'echarts-gl'

export default {
  name: 'Position3DChart',
  props: {
    selectedRecord: {
      type: Object,
      default: () => null
    },
    tableData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      chart: null,
      chartData: [],
      uniqueAmounts: [],
      colorMap: new Map()
    }
  },
  computed: {
    hasData() {
      return this.chartData && this.chartData.length > 0
    },
    
    // 
    cigaretteData() {
      if (!this.selectedRecord || !this.selectedRecord.cigCode) {
        return []
      }
      
      // 
      return this.tableData.filter(item => 
        item.cigCode === this.selectedRecord.cigCode &&
        item.year === this.selectedRecord.year &&
        item.month === this.selectedRecord.month &&
        item.weekSeq === this.selectedRecord.weekSeq
      )
    }
  },
  watch: {
    selectedRecord: {
      handler() {
        this.updateChartData()
      },
      immediate: true
    },
    tableData: {
      handler() {
        this.updateChartData()
      },
      deep: true
    }
  },
  mounted() {
    // DOM
    this.$nextTick(() => {
      setTimeout(() => {
        this.initChart()
      }, 100)
    })
  },
  beforeUnmount() {
    if (this.chart) {
      this.chart.dispose()
    }
  },
  methods: {
    initChart() {
      if (!this.$refs.chartContainer) {
        console.warn('Chart container not found')
        return
      }
      
      // DOM
      const container = this.$refs.chartContainer
      if (container.clientWidth === 0 || container.clientHeight === 0) {
        console.warn('Chart container has no width or height, retrying...')
        // 
        setTimeout(() => {
          this.initChart()
        }, 200)
        return
      }
      
      console.log('Initializing chart with container size:', {
        width: container.clientWidth,
        height: container.clientHeight
      })
      
      try {
        this.chart = echarts.init(this.$refs.chartContainer)
        this.updateChartData()
        
        // 
        window.addEventListener('resize', this.handleResize)
      } catch (error) {
        console.error('Error initializing chart:', error)
      }
    },
    
    //  - 
    reinitChart() {
      console.log('Reinitializing chart...')
      if (this.chart) {
        this.chart.dispose()
        this.chart = null
      }
      
      this.$nextTick(() => {
        setTimeout(() => {
          this.initChart()
        }, 50)
      })
    },
    
    handleResize() {
      if (this.chart) {
        this.chart.resize()
      }
    },
    
    updateChartData() {
      this.processData()
      this.renderChart()
    },
    
    processData() {
      const data = []
      const areas = new Set()
      const amounts = new Set()
      
      // 
      this.cigaretteData.forEach(record => {
        const area = record.deliveryArea || ''
        areas.add(area)
        
        // 30 (D1-D30)
        for (let i = 1; i <= 30; i++) {
          const positionKey = `d${i}`
          const positionValue = record[positionKey] || 0
          
          if (positionValue > 0) {
            data.push([
              area,        // x
              `D${i}`,     // y
              positionValue // z
            ])
            amounts.add(positionValue)
          }
        }
      })
      
      this.chartData = data
      this.areaList = Array.from(areas)
      
      // 
      this.uniqueAmounts = Array.from(amounts).sort((a, b) => b - a) // 
      this.generateColorMap()
      
      console.log(':', {
        data: this.chartData,
        areas: this.areaList,
        uniqueAmounts: this.uniqueAmounts,
        colorMap: Object.fromEntries(this.colorMap),
        selectedRecord: this.selectedRecord
      })
    },
    
    // 
    generateColorMap() {
      this.colorMap.clear()
      
      if (this.uniqueAmounts.length === 0) return
      
      // - 
      const warmColors = [
        '#8B0000', '#B22222', '#DC143C', '#FF0000', '#FF4500', 
        '#FF6347', '#FF7F50', '#FFA500', '#FFB347', '#FFD700'
      ]
      
      // -   
      const coolColors = [
        '#000080', '#0000CD', '#0000FF', '#1E90FF', '#4169E1',
        '#6495ED', '#87CEEB', '#87CEFA', '#ADD8E6', '#E0F6FF'
      ]
      
      if (this.uniqueAmounts.length === 1) {
        // 
        this.colorMap.set(this.uniqueAmounts[0], '#8B0000')
        return
      }
      
      // 
      this.uniqueAmounts.forEach((amount, index) => {
        let color
        
        // 
        const colorIndex = Math.floor((index / 2) % 10) // 10
        
        if (index % 2 === 0) {
          // 
          color = warmColors[colorIndex] || warmColors[warmColors.length - 1]
        } else {
          // 
          color = coolColors[colorIndex] || coolColors[coolColors.length - 1]
        }
        
        this.colorMap.set(amount, color)
      })
      
      console.log(':', Object.fromEntries(this.colorMap))
    },
    
    renderChart() {
      if (!this.chart || !this.hasData) {
        console.warn('Chart not initialized or no data available')
        return
      }
      
      //  (D1-D30)
      const positions = []
      for (let i = 1; i <= 30; i++) {
        positions.push(`D${i}`)
      }
      
      const option = {
        title: {
          text: `${(this.selectedRecord && this.selectedRecord.cigName) || ''} - `,
          left: 'center',
          top: 10,
          textStyle: {
            fontSize: 16,
            fontWeight: 'bold',
            color: '#333'
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: ((colorMap) => {
            return function(params) {
              const [area, position, amount] = params.data
              const color = colorMap.get(amount) || '#8B0000'
              return `: ${area}<br/>: ${position}<br/>: ${amount}<br/><span style="color: ${color};"></span> : ${color}`
            }
          })(this.colorMap)
        },
        legend: {
          show: false
        },
        xAxis3D: {
          type: 'category',
          name: '',
          data: this.areaList,
          nameTextStyle: {
            fontSize: 12,
            color: '#666'
          },
          axisLabel: {
            fontSize: 10,
            color: '#666',
            interval: 0,
            rotate: 45
          }
        },
        yAxis3D: {
          type: 'category',
          name: '',
          data: positions,
          nameTextStyle: {
            fontSize: 12,
            color: '#666'
          },
          axisLabel: {
            fontSize: 10,
            color: '#666'
          }
        },
        zAxis3D: {
          type: 'value',
          name: '',
          nameTextStyle: {
            fontSize: 12,
            color: '#666'
          },
          axisLabel: {
            fontSize: 10,
            color: '#666'
          }
        },
        grid3D: {
          boxWidth: 120,   // 3D
          boxDepth: 120,   // 3D
          boxHeight: 60,
          alpha: 25,
          beta: 40,
          viewControl: {
            projection: 'orthographic',
            autoRotate: false,
            rotateSensitivity: 1,
            zoomSensitivity: 1,
            panSensitivity: 1,
            alpha: 25,
            beta: 40,
            distance: 250   // 
          },
          light: {
            main: {
              intensity: 1.5,
              alpha: 30,
              beta: 40
            },
            ambient: {
              intensity: 0.3
            }
          },
          environment: '#f8f9fa'
        },
        series: [{
          type: 'bar3D',
          data: this.chartData,
          shading: 'lambert',
          // 
          barGap: 0.4,                // 40%
          barCategoryGap: 0.5,        // 50%
          emphasis: {
            label: {
              show: true,
              fontSize: 12,
              fontWeight: 'bold'
            },
            itemStyle: {
              color: '#FFD700' // 
            }
          },
          itemStyle: {
            color: ((colorMap) => {
              return (params) => {
                // 
                const amount = params.data[2]
                return colorMap.get(amount) || '#8B0000' // 
              }
            })(this.colorMap),
            opacity: 0.8,
            // 
            borderWidth: 1,
            borderColor: 'rgba(255,255,255,0.3)'
          },
          label: {
            show: false,
            fontSize: 10
          }
        }]
      }
      
      this.chart.setOption(option, true)
    }
  }
}
</script>

<style scoped>
.position-3d-chart-container {
  width: 100%;
  height: 100%;
  min-height: 1125px;
}

.chart-wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  min-height: 1125px;
}

.chart-container {
  flex: 1;
  height: 100%;
  min-height: 1125px;
}

.chart-legend {
  width: 160px;
  padding: 15px;
  background: #f8f9fa;
  border-left: 1px solid #e4e7ed;
  border-radius: 0 6px 6px 0;
  overflow-y: auto;
}

.legend-title {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  margin-bottom: 12px;
  text-align: center;
  border-bottom: 1px solid #ddd;
  padding-bottom: 8px;
}

.legend-items {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 6px;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.legend-item:hover {
  background-color: #e8f4fd;
}

.legend-color-box {
  width: 16px;
  height: 16px;
  border-radius: 3px;
  border: 1px solid #ccc;
  flex-shrink: 0;
}

.legend-text {
  font-size: 13px;
  color: #333;
  font-weight: 500;
  min-width: 30px;
}

.legend-type {
  font-size: 11px;
  color: #666;
  font-style: italic;
}

.no-data-message {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1125px;
  background: #fafafa;
  border-radius: 6px;
}

:deep(.el-empty__description) {
  color: #999;
  font-size: 14px;
}
</style>
